.mainBody {
    width: 100%;
    height: 100%;
    display: flex;
}

.container {
    width: 140vh;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #FF6E42;
    font-family: "Gugi", sans-serif;
}
.container h1 {
    font-size: 7em;
    margin: 0;
}
.container h2 {
    font-size: 2em;
}